import React, { Component } from 'react';
import './App.css';
import { Side1 } from './Pages/Side';
import { Dummy } from './Pages/data';
import { Filters } from './Pages/Filters';


const month_data_1 = {
    3: "April", 4: "May", 5: "June", 6: "July", 7: "August", 8: "September", 9: "October", 10: "November", 11: "December", 0: "January", 1: "February", 2: "March"

}
var d = new Date();
var n = d.getMonth();
var y = d.getFullYear();

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.dummy_2 = this.dummy_2.bind(this)
        this.state = {
            dataDate: "0",
            dataMonth: month_data_1[n],
            dataYear: y.toString() ,
            filter_1: "jhgj",
            calenderDisplay: "none",
            coverPageDisplay: "inline",
        }
    };

    getStarted() {
        this.setState({ calenderDisplay: "flex" })
        this.setState({ coverPageDisplay: "none" })
        
    
    }

    dummy_2(x, y, z) {
        this.setState({ dataDate: x })
        this.setState({ dataMonth: y })
        this.setState({ dataYear: z })
    }

    filterDummy(a) {
        this.setState({ filter_1: a })
    }


    render() {
        return (

            <div className="coverpage">
                <div  style={{ display: this.state.coverPageDisplay }} >

                    <div className = "cptext"> Compliance Calender</div>
                    <div className = "cptext_1" onClick={this.getStarted.bind(this)}>Get Started</div>
                </div>
                 
                <div className="main" style={{ display: this.state.calenderDisplay }}>
                    <div className="sidebar">
                        <Side1 value={(x, y, z) => { this.dummy_2(x, y, z) }} />
                    </div>
                    <div className="mainbar">
                        <Dummy dataDate={this.state.dataDate} dataMonth={this.state.dataMonth} dataYear={this.state.dataYear} />
                        
                    </div>
                 </div>

             </div>

                
                
            

        );
    }
}

export default Dashboard;
