export function Dummy({ dataDate, dataMonth, dataYear }) {
    
    try {
        var test_1 = year[dataYear][dataMonth][dataDate]["Compliance"].map(x => <p>{x}</p>);

        return <div>
            <div className="data">{test_1}</div>

        </div>
    } catch (err) {
        var a = year[dataYear][dataMonth]["Dates"].map((x) => {
            var d = x + '-' + dataMonth.substring(0, 3) + '-' + dataYear
            return year[dataYear][dataMonth][x]["Group"].map((q) => {
                return <div className="data_content_1">{d}</div>
            })
        })

        var b = year[dataYear][dataMonth]["Dates"].map((x) => {
            return year[dataYear][dataMonth][x]["Group"].map((q) => {
                return <div className="data_content_2">{q}</div>
            })
        })
        
        
        var c = year[dataYear][dataMonth]["Dates"].map((x) => {
                return year[dataYear][dataMonth][x]["Compliance"].map((q) => {
                    return <div className="data_content_3">{q}</div>
                    })
                })
               
        return <div>
            <div>
                <text className="data_h_1">Date</text>
                <text className="data_h_2">Category</text>
                <text className="data_h_3">Compliance</text>
            </div>
            <div className="data_content">
                <div>{a}</div>
                <div>{b}</div>
                <div>{c}</div>
            </div>
            


        </div >
        }
        

        
    

    
}

