import { Component} from "react"
const options = ["Individual", "Hindu Undivided Families", "Association of Persons [AOPs]", "Body of individuals [BOIs]", "Firms / LLPs", "Companies", "Local authority", "Public sector undertakings[PSUs]", "Central / State Government", "E Commerce companies", "Society"];
export class Filters extends Component {
    state = {
        isActive: false,
        filter_1: "Choose One"
    }
    filterChildFunction = (a) => {
        this.setState({ filter_1: a.target.getAttribute("name") });
        this.props.value(this.state.filter_1);
        this.setState({ isActive: false })
       
    }



    render(){
        return (
            
            < div className="df">
                <div className="df-btn" onClick={(e) => this.setState({ isActive: true })} >{this.state.filter_1}</div>
                {this.state.isActive && (
                    <div className="df-content">
                        {options.map((option) => {
                            return <div className="df-item" name={option} onClick={this.filterChildFunction.bind(this)} >
                                {option}
                            </div>
                        })}

                    </div>
                )}
            </div >
        )

    }
}

